<template>
  <div>
    <div>
      <input
        v-model="newTodo"
        @keyup.enter="addTodo"
        placeholder="新しいタスク"
      >
      <button
        @click="addTodo"
      >
        追加  
      </button>      
    </div>

    <ul>
      <li
        v-for="(todo, index) in todos"
        :key="index"
      >
        <input
          type="checkbox"
          v-model="todo.completed"
          @change="updateLocalStorage"
        >
        <span>
          {{ todo.name }}
        </span>
        <button
          @click="removeTodo(index)"
        >
          削除
        </button>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  data() {
    return {
      newTodo: '',
      todos: []
    }
  },
  methods: {
    addTodo() {
      if (this.newTodo.trim().length === 0) return

      this.todos.push({
        name: this.newTodo,
        completed: false
      })

      this.updateLocalStorage()
      this.newTodo = ''
    },
    removeTodo(index) {
      this.todos.splice(index, 1)
      this.updateLocalStorage()
    },
    updateLocalStorage() {
      localStorage.setItem('todos', JSON.stringify(this.todos))
    }
  },
  created() {
    const savedTodos = localStorage.getItem('todos')
    if (savedTodos) {
      this.todos = JSON.parse(savedTodos)
    }
  }
}
</script>

